import React from 'react';

const InvestBox = (props) => {
  return (
    <div className="InvestBox">
      <label className="labelinput" htmlFor="simulatewithinput">
        Simulate with
      </label>
      <span className="dollarAndInput">
        <span className="dollar">$</span>
        <input
          id="simulatewithinput"
          type="number"
          value={props.preInvestment}
          onChange={(e) => props.onInvestmentChange(e.target.value)}
        />
      </span>
    </div>
  );
};

export default InvestBox;
