import React from 'react';
import { css } from '@emotion/react';
import ClipLoader from 'react-spinners/ClipLoader';
// import { bmatches } from '../test';
import './SearchBar.css';

const override = css`
  position: absolute;
  top: 3px;
  right: 6px;
  display: block;
  margin: 0 auto;
`;

class SearchBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = { search: '', matches: [], loading: false };
  }
  onOptClick = (symbol) => {
    this.props.onOptionClick(symbol);
    this.setState({ matches: [] });
  };

  onSearchChange = (e) => {
    if (e.target.value === '') this.setState({ loading: false });
    else this.setState({ loading: true });

    this.setState({ search: e.target.value });
    if (e.target.value !== '') {
      if (this.timer) clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        if (e.target.value !== '') {
          fetch(`https://yh-finance.p.rapidapi.com/auto-complete?q=${e.target.value}&region=US`, {
            method: 'GET',
            headers: {
              'x-rapidapi-host': 'yh-finance.p.rapidapi.com',
              'x-rapidapi-key': 'e5fb796200msh87d00089c67d477p1afbb8jsn4d6ef84a5a85',
            },
          })
            .then((res) => res.json())
            .then((data) => {
              // const usdFiltered = data.bestMatches
              //   .filter((x) => x['8. currency'] === 'USD')
              //   .map((y) => {
              //     return { symbol: y['1. symbol'], name: y['2. name'] };
              //   });
              // this.setState({ ...this.state, matches: usdFiltered });
              const results = [];
              if (data.quotes.length > 4) {
                for (let i = 0; i <= 3; i++) {
                  results.push({ symbol: data.quotes[i].symbol, name: data.quotes[i].longname });
                }
              } else if (data.quotes.length >= 1 && data.quotes <= 4) {
                for (let i = 0; i < data.quotes.length; i++) {
                  results.push({ symbol: data.quotes[i].symbol, name: data.quotes[i].longname });
                }
              }

              this.setState({ ...this.state, matches: results }, () =>
                this.setState({ loading: false })
              );
            })

            .catch((err) => {
              console.error(err);
            });
        }
      }, 500);
    }
  };

  renderOptions() {
    return (
      <div className="SuggestionsBox">
        {this.state.matches.map((x) => {
          if (this.props.alreadyPre.indexOf(x.symbol) === -1) {
            return (
              <div
                className="SuggestionsEach"
                key={`${x.symbol}key`}
                onClick={() => this.onOptClick(x.symbol)}>
                <span className="SuggestionsSymbol">{x.symbol}</span>
                <span className="SuggestionsName">{x.name}</span>
              </div>
            );
          } else return null;
        })}
      </div>
    );
  }

  componentDidUpdate(prevProps) {
    if (prevProps.alreadyPre !== this.props.alreadyPre) {
      this.setState({ search: '' });
    }
  }

  render() {
    return (
      <div className="SearchBarBox">
        <label className="labelinput" htmlFor="searchbar">
          Search Stock:{' '}
        </label>
        <div className="inputDiv">
          <input
            id="searchbar"
            type="text"
            value={this.state.search}
            onChange={this.onSearchChange}></input>
          <ClipLoader color="#00a8ff" loading={this.state.loading} css={override} size={12} />
        </div>
        {this.state.matches.length > 0 && this.renderOptions()}
      </div>
    );
  }
}

export default SearchBar;
