import React from 'react';
import Plot from 'react-plotly.js';

const LineChart = (props) => {
  const renderLineChart = () => {
    return (
      <Plot
        data={props.data}
        layout={{
          width: props.width,
          height: props.height,
          title: { text: props.title, font: { color: '#fff' } },
          legend: { font: { color: '#fff' } },
          font: { color: '#fff' },
          yaxis: { title: props.yaxisTitle, gridcolor: '#555' },
          xaxis: { gridcolor: '#555' },
          paper_bgcolor: '#2f3640',
          plot_bgcolor: '#2f3640',
        }}
      />
    );
  };
  return <div>{renderLineChart()}</div>;
};

export default LineChart;
