import React from 'react';

const TextNormalizedPriceChart = (props) => {
  const lenX = props.data[0]['x'].length;
  const startDate = props.data[0]['x'][0];
  const endDate = props.data[0]['x'][lenX - 1];

  const renderResults = () => {
    return (
      <ul className="unordered">
        {props.data.map((x, i) => {
          const lenY = x['y'].length;
          const startPrice = x['y'][0];
          const endPrice = x['y'][lenY - 1].toFixed(4);
          const finalResult = endPrice - startPrice;
          let resultStr = '';
          let operator = '';
          let color = '';
          if (finalResult > 0) {
            color = 'limegreen';
            resultStr = (
              <span>
                a <span style={{ color, fontWeight: 'bold' }}>increase</span> of{' '}
                <span style={{ color, fontWeight: 'bold' }}>
                  ${(finalResult * 100).toFixed(2)}%
                </span>
              </span>
            );
            operator = '+';
          } else if (finalResult < 0) {
            color = 'red';
            resultStr = (
              <span>
                a <span style={{ color, fontWeight: 'bold' }}>decrease</span> of{' '}
                <span style={{ color, fontWeight: 'bold' }}>
                  ${(finalResult * 100 * -1).toFixed(2)}%
                </span>
              </span>
            );
            operator = '-';
          } else if (finalResult === 0) {
            resultStr = 'maintaining the price.';
            operator = '+';
          }
          return (
            <li className="liresult" key={`normalized${i}`}>
              {x['name']} went from {startPrice} to{' '}
              <span style={{ color, fontWeight: 'bold' }}>{endPrice}</span>, {resultStr}. With $
              {props.investment} invested, you'd end up with{' '}
              <span style={{ color, fontWeight: 'bold' }}>
                ${props.endMoney[x['name']][1].toFixed(2)}
              </span>{' '}
              (
              <span style={{ color, fontWeight: 'bold' }}>
                {operator}${Math.abs(props.endMoney[x['name']][1] - props.investment).toFixed(2)})
              </span>
            </li>
          );
        })}
      </ul>
    );
  };
  return (
    <div>
      <p className="textpar">
        This chart is similar to the simple Price Chart, but now all the prices starts at 100% and
        all the other days represent prices relatively to the first day. It gives us a better idea
        about what could happen with our money if we did invest in those stocks in a specific period
        of time. Let's play around with this chart to get some insights.
      </p>
      <p className="FromTo">
        From <span className="date">{startDate}</span> to <span className="date">{endDate}</span>:{' '}
      </p>
      <div className="resultsBox">{renderResults()}</div>
    </div>
  );
};

export default TextNormalizedPriceChart;
