import React from 'react';
import Plot from 'react-plotly.js';

const HistChart = (props) => {
  const renderHistChart = () => {
    return (
      <Plot
        data={props.data}
        layout={{
          width: props.width,
          height: props.height,
          xaxis: { title: props.xaxisTitle, gridcolor: '#555' },
          yaxis: { title: props.yaxisTitle, gridcolor: '#555' },
          title: { text: props.title, font: { color: '#fff' } },
          legend: { font: { color: '#fff' } },
          font: { color: '#fff' },
          paper_bgcolor: '#2f3640',
          plot_bgcolor: '#2f3640',
        }}
      />
    );
  };
  return <div>{renderHistChart()}</div>;
};

export default HistChart;
