import React from 'react';

const TextDividendsChart = (props) => {
  const lenX = props.data[0]['x'].length;
  const startDate = props.data[0]['x'][0];
  const endDate = props.data[0]['x'][lenX - 1];

  const renderResults = () => {
    return (
      <ul>
        {props.data.map((x) => {
          return (
            <li className="liresult" key={`dividends${x['name']}`}>
              {x['name']} paid a total amount of{' '}
              <span style={{ color: 'limegreen', fontWeight: 'bold' }}>
                ${props.endMoney[x['name']][2].toFixed(2)}
              </span>{' '}
              in dividends.
            </li>
          );
        })}
      </ul>
    );
  };
  return (
    <div>
      <p className="textpar">
        Now, let's talk about dividends! This bar chart shows the amount of dividends paid in the
        selected period.
      </p>
      <p className="FromTo">
        From <span className="date">{startDate}</span> to <span className="date">{endDate}</span>,
        with ${props.investment} invested:
      </p>
      <div className="resultsBox">{renderResults()}</div>
    </div>
  );
};

export default TextDividendsChart;
