import React from 'react';
import Plot from 'react-plotly.js';

const BarChart = (props) => {
  const renderBarChart = () => {
    return (
      <Plot
        data={props.data}
        layout={{
          width: props.width,
          height: props.height,
          title: { text: props.title, font: { color: '#fff' } },
          legend: { font: { color: '#fff' } },
          font: { color: '#fff' },
          xaxis: { tickmode: 'linear', dtick: 1, gridcolor: '#555' },
          yaxis: { title: props.yaxisTitle, gridcolor: '#555' },
          paper_bgcolor: '#2f3640',
          plot_bgcolor: '#2f3640',
        }}
      />
    );
  };
  return <div>{renderBarChart()}</div>;
};

export default BarChart;
