import React from 'react';

const Preselected = (props) => {
  const renderPreselected = () => {
    return props.preselected.map((x, i) => {
      return (
        <div className="PreselectedLine" key={`${x}del`}>
          <span className="PreselectedSymbol">
            {i + 1}. {x}
          </span>{' '}
          <span className="delbtn" onClick={() => props.delOptionClick(x)}>
            &times;
          </span>
        </div>
      );
    });
  };
  return (
    <div className="PreselectedBox">{props.preselected.length > 0 && renderPreselected()}</div>
  );
};

export default Preselected;
