import React, { useState, useEffect, useRef } from 'react';
import Welcome from './Welcome';
import SearchBar from './SearchBar';
import InvestBox from './InvestBox';
import Preselected from './Preselected';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import LineChart from './LineChart';
import BarChart from './BarChart';
import HistChart from './HistChart';
import BoxChart from './BoxGraph';
import TextPriceChart from './TextPriceChart';
import TextNormalizedPriceChart from './TextNormalizedPriceChart';
import TextDividendsChart from './TextDividendsChart';
import TextHistogramChart from './TextHistogramChart';
import TextBoxPlot from './TextBoxPlot';
import Footer from './Footer';
import { initialPriceChartData } from '../initialPriceChartData';
import { initialPriceNormalizedData } from '../initialPriceNormalizedData';
import { initialDividendsData } from '../initialDividendsData';
import { initialHistData } from '../initialHistData';
import { initialBoxData } from '../initialBoxData';
import { initialEndMoney } from '../initialEndMoney';
import { css } from '@emotion/react';
import ClimbingBoxLoader from 'react-spinners/ClimbingBoxLoader';
import './Graph.css';
import Thankyou from './Thankyou';

const Graph = () => {
  const [preInvestment, setPreInvestment] = useState(1000);
  const [preselection, setPreselection] = useState([]);
  const [startDateInput, setStartDateInput] = useState(new Date('2019-01-02'));
  const [endDateInput, setEndDateInput] = useState(new Date());
  const [startDate, setStartDate] = useState(new Date('2019-01-02'));
  const [endDate, setEndDate] = useState(new Date());
  const [stocksFetched, setStocksFetched] = useState({});
  const [stocksToSearch, setStocksToSearch] = useState([]);
  const [toShow, setToShow] = useState({});
  const [dates, setDates] = useState([]);
  const [investment, setInvestment] = useState(1000);
  const [priceData, setPriceData] = useState([]);
  const [priceNormalizedData, setPriceNormalizedData] = useState([]);
  const [dividendsData, setDividendsData] = useState([]);
  const [histData, setHistData] = useState([]);
  const [boxData, setboxData] = useState([]);
  const [endMoneyObj, setEndMoneyObj] = useState({});
  const [startApp, setStartApp] = useState(false);
  const [fetching, setFetching] = useState('');
  // const [priceStart, setPriceStart] = useState([]);
  // const [priceEnd, setPriceEnd] = useState([]);
  // const [normalizedPriceStart, setNormalizedPriceStart] = useState([]);
  // const [normalizedPriceEnd, setNormalizedPriceEnd] = useState([]);

  function usePrevious(value) {
    const ref = useRef();
    useEffect(() => {
      ref.current = value;
    });
    return ref.current;
  }
  const prevStartDate = usePrevious(startDate);
  const prevEndDate = usePrevious(endDate);

  const dateToTs = (str) => {
    const dateStr = str.toISOString().split('T')[0].split('-').splice(0.3);
    const newDate = new Date(dateStr[0], dateStr[1] - 1, dateStr[2]);
    return newDate.getTime() / 1000;
  };

  const addOption = (option) => {
    setPreselection([...preselection, option]);
  };

  const delOption = (option) => {
    const preCopy = [...preselection];
    const index = preCopy.indexOf(option);
    preCopy.splice(index, 1);
    setPreselection(preCopy);
  };

  const pushToShow = (stock, start, end) => {
    const closes = [];
    const dividends = [];
    for (let i = 0; i < Object.keys(stocksFetched[stock]).length; i++) {
      closes.push(stocksFetched[stock][Object.keys(stocksFetched[stock])[i]]['close']);
      dividends.push(stocksFetched[stock][Object.keys(stocksFetched[stock])[i]]['dividend']);
    }

    const newObj = { ...toShow, [stock]: { closes, dividends } };

    setToShow(newObj);

    if (JSON.stringify(dates) !== JSON.stringify(Object.keys(stocksFetched[stock]))) {
      const newDates = Object.keys(stocksFetched[stock]);
      setDates(newDates);
    }
  };

  const fetchStock = (stock, startTimestamp, endTimestamp) => {
    // if (Object.keys(stocksFetched).indexOf(stock) === -1) {
    //   console.log('FETCHING');
    //   fetch(
    //     `https://alpha-vantage.p.rapidapi.com/query?function=TIME_SERIES_DAILY_ADJUSTED&symbol=${stock}&outputsize=full&datatype=json`,
    //     {
    //       method: 'GET',
    //       headers: {
    //         'x-rapidapi-host': 'alpha-vantage.p.rapidapi.com',
    //         'x-rapidapi-key': 'e5fb796200msh87d00089c67d477p1afbb8jsn4d6ef84a5a85',
    //       },
    //     }
    //   )
    //     .then((res) => res.json())
    //     .then((data) => {
    //       for (let i = Object.keys(data['Time Series (Daily)']).length - 1; i >= 0; i--) {
    //         if (Object.keys(data['Time Series (Daily)'])[i] < '2010')
    //           delete data['Time Series (Daily)'][Object.keys(data['Time Series (Daily)'])[i]];
    //         else break;
    //       }
    //       const newStocksFetched = { ...stocksFetched, [stock]: data['Time Series (Daily)'] };
    //       setStocksFetched(newStocksFetched);
    //     })
    //     .catch((stock) => {
    //       console.error(`Could not find any ${stock} stock information`);
    //     });
    // }

    if (Object.keys(stocksFetched).indexOf(stock) === -1) {
      fetch(
        `https://yh-finance.p.rapidapi.com/stock/v2/get-chart?interval=1d&symbol=${stock}&region=US&period1=${startTimestamp}&period2=${endTimestamp}`,
        {
          method: 'GET',
          headers: {
            'x-rapidapi-host': 'yh-finance.p.rapidapi.com',
            'x-rapidapi-key': 'e5fb796200msh87d00089c67d477p1afbb8jsn4d6ef84a5a85',
          },
        }
      )
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          const newObj = {};

          const convertedDates = data.chart.result[0].timestamp.map(
            (x) => new Date(x * 1000).toISOString().split('T')[0]
          );

          for (let i = 0; i < convertedDates.length - 1; i++) {
            newObj[convertedDates[i]] = {
              close: data.chart.result[0].indicators.quote[0].close[i],
              dividend: 0.0,
            };
          }

          if (
            Object.keys(data.chart.result[0]).indexOf('events') !== -1 &&
            Object.keys(data.chart.result[0].events).indexOf('dividends') !== -1
          ) {
            const fetchedDividends = Object.keys(data.chart.result[0].events.dividends);

            for (let i = 0; i < fetchedDividends.length; i++) {
              const newDividendDate = new Date(fetchedDividends[i] * 1000)
                .toISOString()
                .split('T')[0];
              const newDividendAmount =
                data.chart.result[0].events.dividends[fetchedDividends[i]]['amount'];
              newObj[newDividendDate] = {
                ...newObj[newDividendDate],
                dividend: newDividendAmount,
              };
            }
          }
          const newStocksFetched = { ...stocksFetched, [stock]: newObj };
          setStocksFetched(newStocksFetched);

          if (JSON.stringify(dates) !== JSON.stringify(Object.keys(newStocksFetched[stock]))) {
            const newDates = Object.keys(newStocksFetched[stock]);
            setDates(newDates);
          }
        })
        .catch((stock) => {
          console.error(`Could not find any ${stock} stock information`);
        });
    }
  };

  const compareClickHandler = () => {
    if (startDate !== startDateInput || endDate !== endDateInput) {
      setStocksFetched({});
      setToShow({});
      setStartDate(startDateInput);
      setEndDate(endDateInput);
    }

    // setStocksToSearch(search.toUpperCase().replace(/ /g, '').split(','));
    setStocksToSearch(preselection);
    setInvestment(preInvestment);
    setStartApp(true);
  };

  useEffect(() => {
    if (startDate !== prevStartDate || endDate !== prevEndDate) {
      setStocksFetched({});
      setToShow({});
    }

    const alreadyFetched = Object.keys(stocksFetched);
    const remain = stocksToSearch.filter((x) => alreadyFetched.indexOf(x) === -1);
    if (remain.length > 0) fetchStock(remain[0], dateToTs(startDate), dateToTs(endDate));

    if (
      (Object.keys(stocksFetched).length > 0 &&
        Object.keys(stocksFetched).length !== Object.keys(toShow).length) ||
      stocksToSearch.length < Object.keys(toShow).length ||
      startDate !== prevStartDate ||
      endDate !== prevEndDate
    ) {
      const remainToShow = Object.keys(stocksFetched).filter(
        (x) => Object.keys(toShow).indexOf(x) === -1 && stocksToSearch.indexOf(x) !== -1
      );
      if (remainToShow.length > 0) pushToShow(remainToShow[0], startDate, endDate);

      if (stocksToSearch.length < Object.keys(toShow).length) {
        Object.keys(toShow).forEach((x) => {
          if (stocksToSearch.indexOf(x) === -1) {
            const newObj = { ...toShow };
            delete newObj[x];
            setToShow(newObj);
          }
        });
      }
    }
  }, [stocksToSearch, stocksFetched, startDate, endDate, toShow]);

  useEffect(() => {
    let newPriceData;
    let newPriceNormalizedData;
    let newDividendsData;
    let newHistData;
    let newBoxData;
    let newPriceStart = [];
    let newPriceEnd = [];
    let newNormalizedPriceStart = [];
    let newNormalizedPriceEnd = [];
    let endMoney = {};

    if (Object.keys(toShow).length > 0) {
      Object.keys(toShow).forEach((each) => {
        const n_stocks = investment / Number(toShow[each]['closes'][0]);
        const end_money =
          n_stocks * Number(toShow[each]['closes'][toShow[each]['closes'].length - 1]);
        // n_stocks, end_money, quanto ganhou com dividendos
        endMoney[each] = [n_stocks, end_money, 0];
      });

      newPriceData = Object.keys(toShow).map((each) => {
        return {
          x: dates,
          y: toShow[each]['closes'],
          type: 'scatter',
          name: each,
        };
      });

      newPriceNormalizedData = Object.keys(toShow).map((each) => {
        return {
          x: dates,
          y: toShow[each]['closes'].map((x) => x / toShow[each]['closes'][0]),
          type: 'scatter',
          name: each,
        };
      });

      const firstYear = Number(dates[0].split('-')[0]);
      const lastYear = Number(dates[dates.length - 1].split('-')[0]);
      const yearsArray = [];
      for (let i = firstYear; i <= lastYear; i++) {
        yearsArray.push(i);
      }

      const divObj = {};
      Object.keys(toShow).forEach((each) => {
        divObj[each] = [];
        for (let i = 0; i < yearsArray.length; i++) divObj[each].push(0);
      });

      Object.keys(toShow).forEach((each) => {
        toShow[each]['dividends'].forEach((x, i) => {
          if (Number(x) >= 0) {
            const actualPosition = Number(dates[i].split('-')[0]) - firstYear;
            const totalDiv = Number(x) * endMoney[each][0];
            divObj[each][actualPosition] += totalDiv;
            endMoney[each][2] += totalDiv;
          }
        });
      });

      newDividendsData = Object.keys(toShow).map((each) => {
        return {
          x: yearsArray,
          y: divObj[each],
          type: 'bar',
          name: each,
        };
      });

      newHistData = Object.keys(toShow).map((each) => {
        return {
          x: toShow[each]['closes'],
          type: 'histogram',
          name: each,
          opacity: 0.75,
        };
      });

      newBoxData = Object.keys(toShow).map((each) => {
        return {
          y: toShow[each]['closes'],
          type: 'box',
          name: each,
        };
      });

      Object.keys(toShow).forEach((x) => {
        newPriceStart.push(toShow[x]['closes'][0]);
        newPriceEnd.push(toShow[x]['closes'][toShow[x]['closes'].length - 1]);
        newNormalizedPriceStart.push(1);
        newNormalizedPriceEnd.push(
          toShow[x]['closes'][toShow[x]['closes'.length - 1]] / toShow[x]['closes'][0]
        );
      });

      // navigator.clipboard.writeText(JSON.stringify(endMoney));
    } else {
      newPriceData = [];
      newPriceNormalizedData = [];
      newDividendsData = [];
      newHistData = [];
    }
    setPriceData(newPriceData);
    setPriceNormalizedData(newPriceNormalizedData);
    setDividendsData(newDividendsData);
    setHistData(newHistData);
    setboxData(newBoxData);
    setEndMoneyObj(endMoney);

    // setPriceStart(newPriceStart);
    // setPriceEnd(newPriceEnd);
    // setNormalizedPriceStart(newNormalizedPriceStart);
    // setNormalizedPriceEnd(newNormalizedPriceEnd);
  }, [toShow, investment]);

  useEffect(() => {
    if (
      stocksToSearch.length > 0 &&
      JSON.stringify(stocksToSearch) !== JSON.stringify(Object.keys(toShow))
    ) {
      const remain = stocksToSearch.filter((x) => Object.keys(toShow).indexOf(x) === -1);
      setFetching(remain[0]);
    } else setFetching('');
  }, [stocksToSearch, toShow]);

  let priceDataProps;
  let priceNormalizedDataProps;
  let dividendsDataProps;
  let histDataProps;
  let boxDataProps;
  let endMoneyProps;

  if (startApp) {
    priceDataProps = priceData;
    priceNormalizedDataProps = priceNormalizedData;
    dividendsDataProps = dividendsData;
    histDataProps = histData;
    boxDataProps = boxData;
    endMoneyProps = endMoneyObj;
  } else {
    priceDataProps = initialPriceChartData;
    priceNormalizedDataProps = initialPriceNormalizedData;
    dividendsDataProps = initialDividendsData;
    histDataProps = initialHistData;
    boxDataProps = initialBoxData;
    endMoneyProps = initialEndMoney;
  }

  let wid = 750;
  let hei = 500;
  if (window.screen.width < 410) {
    wid = 350;
    hei = 300;
  } else if (window.screen.width < 510) {
    wid = 420;
    hei = 330;
  } else if (window.screen.width < 610) {
    wid = 500;
    hei = 380;
  } else if (window.screen.width < 750) {
    wid = 600;
    hei = 470;
  }

  return fetching === '' &&
    JSON.stringify(stocksToSearch) === JSON.stringify(Object.keys(toShow)) ? (
    <div className="Graph" style={{ paddingTop: startApp ? '3.2rem' : '0' }}>
      {!startApp && <Welcome />}

      {preselection.length < 5 && <SearchBar onOptionClick={addOption} alreadyPre={preselection} />}

      <div className="PreselectedAndInvestBox">
        <Preselected preselected={preselection} delOptionClick={delOption} />
        <InvestBox preInvestment={preInvestment} onInvestmentChange={setPreInvestment} />
      </div>

      <div className="DatePickers">
        <div className="StartDateBox">
          <label htmlFor="StartDate" className="labelinput">
            Start Date
          </label>
          <DatePicker
            id="StartDate"
            selected={startDateInput}
            onChange={(date) => setStartDateInput(date)}
            dateFormat="yyyy/MM/dd"
            minDate={new Date('2001-01-02')}
            maxDate={new Date()}
          />
        </div>

        <div className="EndDateBox">
          <label className="labelinput" htmlFor="EndDate">
            End Date
          </label>
          <DatePicker
            id="EndDate"
            selected={endDateInput}
            onChange={(date) => setEndDateInput(date)}
            dateFormat="yyyy/MM/dd"
            minDate={new Date('2001-01-02')}
            maxDate={new Date()}
          />
        </div>
      </div>

      <button
        className="analyzebtn"
        onClick={compareClickHandler}
        disabled={preselection.length === 0 || startDateInput >= endDateInput}>
        ANALYZE!
      </button>

      <div className="ChartBox">
        <LineChart
          data={priceDataProps}
          width={wid}
          height={hei}
          title="Price Chart"
          yaxisTitle={'Stock Price ($)'}
        />
      </div>
      <div className="GraphText">
        <TextPriceChart />
      </div>

      <div className="ChartBox">
        <LineChart
          data={priceNormalizedDataProps}
          width={wid}
          height={hei}
          title="Normalized Price Chart"
          yaxisTitle={'Normalized Price'}
        />
      </div>
      <div className="GraphText">
        <TextNormalizedPriceChart
          data={priceNormalizedDataProps}
          endMoney={endMoneyProps}
          investment={investment}
        />
      </div>

      <div className="ChartBox">
        <BarChart
          data={dividendsDataProps}
          width={wid}
          height={hei}
          title={`Dividends received (with $${investment} invested)`}
          yaxisTitle={`Dividends ($)`}
        />
      </div>
      <div className="GraphText">
        <TextDividendsChart
          data={priceNormalizedDataProps}
          endMoney={endMoneyProps}
          investment={investment}
        />
      </div>

      <div className="ChartBox">
        <HistChart
          data={histDataProps}
          width={wid}
          height={hei}
          title="Price Distribution"
          xaxisTitle={'Price ($)'}
          yaxisTitle={`Number of Days`}
        />
      </div>
      <div className="GraphText">
        <TextHistogramChart />
      </div>

      <div className="ChartBox">
        <BoxChart
          data={boxDataProps}
          width={wid}
          height={hei}
          title="Statistical Analysis"
          yaxisTitle={`Stock Price ($)`}
        />
      </div>
      <div className="GraphText">
        <TextBoxPlot />
      </div>

      <Thankyou />

      <Footer />
    </div>
  ) : (
    <div className="gettingdata">
      <div className="innergettingdata">
        <p>
          Getting <span className="fetching">{fetching}</span> data...
        </p>
        <ClimbingBoxLoader color="#00a8ff" css={override} size={25} />
      </div>
    </div>
  );
  // return (
  //   <div className="gettingdata">
  //     <div className="innergettingdata">
  //       <p>{`Getting data...`}</p>
  //       <ClimbingBoxLoader color="#00a8ff" css={override} size={25} />
  //     </div>
  //   </div>
  // );
};

const override = css`
  /* position: absolute; */
  top: 3px;
  right: 6px;
  display: block;
  margin: 0 auto 3.2rem;
`;

export default Graph;
